/* Overall container */
.future-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 60%;
  margin: 0 auto;
  padding-bottom: 10rem;
  color: black;
}

/* Future background */
.future-background {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-top: 10rem;
  opacity: 100% ;
}
.future-background img { /* Target the background image directly */
  opacity: 0.1; /* Lighten the background image by 40% */
  position: absolute;
  width: 60%;
  height: 100%;
  z-index: -1; /* Place the background image behind the content */
}

/* Future title */
.future-title h1 {
  font-size: 5rem;
  font-weight: bold;
  margin-bottom: 20px;
}

/* Future content */
.future-content {
  text-align: center;
  line-height: 1.5;
  font-size: 1.5rem;
}
.future-content p {
  width: 50%;
}
.future-content ul {
  list-style: none; 
  text-align: left;
  margin: 0;
  padding: 0;
}

.future-content li {
  margin-bottom: 2rem;
}

@media (max-width: 768px) {

  .future-container {
    width: 95%;
    margin: 0 auto;
    padding-top: 5rem;
    margin-bottom: 0;
    padding-bottom: 0;
  }

  .future-background {
    background-color: #f2f2f2;
    padding: 30px;
    border-radius: 10px;
  }

  .future-background img { /* Target the background image directly */
    opacity: 1; /* Lighten the background image by 40% */
    position: relative;
    width: 100%;
    height: 100%;
    z-index: 0; /* Place the background image behind the content */
  }

  .future-title {
    text-align: center;
  }

  .future-title h1 {
    font-size: 3rem;
    margin-bottom: 10px;
  }

  .future-content h3 {
    font-size: 1.3rem;
    margin-bottom: 10px;
  }

  .future-content ul {
    list-style: disc;
    margin-bottom: 20px;
  }

  .future-content li {
    font-size: 1.3rem;
    line-height: 1.2;
    margin-bottom: 10px;
  }
  
}