.randompic-container {
    display: flex;;
    justify-content: center;
    flex-wrap: wrap;
    background-color:#f2f2f2;
    text-align: center;
    border-top: 10px solid #205084;
}

.randompic-container img {
    width: auto;
    height: 40rem;
    object-fit: contain;
    margin: 10px;
    position: relative;
    transition: all 0.3s ease;
    border-radius: 10px;
    box-shadow: 0px 0px 15px 5px rgba(0, 0, 0, 0.75);
}
.randompic-container h2 {
    font-size: 3rem;
    font-weight: 700;
    margin: 2rem;
    color: #205084;
    text-align: center;
    
}

.randompic-container img:hover {
    transform: scale(1.05);
}

.randompic-container img::before {
    content: '';
    position: absolute;
    top: 50%;
    left: 10%;
    border: solid;
    border-width: 0 3px 3px 0;
    display: none;
    padding: 5px;
    transform: rotate(135deg);
    -webkit-transform: rotate(135deg);
}

.randompic-container img:hover::before {
    display: inline;
    border-color: #ffffff;
}
@media (max-width: 768px) {
    .randompic-container {
      flex-direction: column;
      align-items: center;
    }
  
    .randompic-container img {
      width: 90%;
      height: auto;
    }
  
    .randompic-container img::before {
      left: 45%;
    }
  }